.mapContainer {
    max-width: 800px;
    display: flex;
    flex-wrap: wrap;
  }
  .mountain {
    width: 100px;
    height: 100px;
    background-color: grey;
  }
  .resource {
    width: 100px;
    height: 100px;
    background-color: green;
  }
  .special {
    width: 100px;
    height: 100px;
    background-color: yellow;
  }
  .button {
    background-color: #feda4a !important;
    color: black !important;
  }
  .slider {
    color: #feda4a !important;
  }
  .buttonHolder {
    display: flex;
    width: 100%;
    padding: 12px;
    justify-content: space-between;
    color: white;
    font-size: 30px;
    font-weight: bold;
    gap: 12px;
  }
.home__container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 0px 20px;
}
.logo{
    width: 100%;
    max-width: 700px;
}
.starWarsFontMedium {
    color: #feda4a;
    font-family: 'Pathway Gothic One', sans-serif;
    font-size: 1em;
    font-weight: 600;
    letter-spacing: 6px;
    line-height: 150%;
    text-align: center;
}
.header {
margin-bottom: 20px;
background: rgb(93,93,93);
background: linear-gradient(0deg, rgba(93,93,93,0) 0%, rgba(38,113,0,0) 12%, rgba(107,129,97,1) 43%, rgba(135,135,135,1) 100%);
height: 150px;
}
.tab {
    margin: 0px !important;
    padding: 0px !important;
    max-width: 100%;

}
.tabs {
    color: #feda4a !important;
    font-size: 1.3em !important;
    font-weight: 600 !important;
    text-transform: none !important;
    letter-spacing: 6px;
    text-align: center;
    margin: 12px !important;
    margin-bottom: -12px !important;
    padding: 0px !important;
}
.selectedTab {
    color: #d2a819cc !important;
    font-size: 1.3em !important;
    font-weight: 600 !important;
    text-transform: none !important;
    letter-spacing: 6px;
    text-align: center;
    margin: 12px !important;
    margin-bottom: -12px !important;
    padding: 0px !important;
}
.tabHolder {
    width: 100%;
    max-width: 400px;
    display: flex;
    justify-content: space-between;
}


  @media (max-width: 800px)
  {
    .mapContainer {
        max-width: 400px;
        display: flex;
        flex-wrap: wrap;
      }
      .mountain {
        width: 50px;
        height: 50px;
        background-color: grey;
      }
      .resource {
        width: 50px;
        height: 50px;
        background-color: green;
      }
      .special {
        width: 50px;
        height: 50px;
        background-color: yellow;
      }
      .buttonHolder {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 12px;
        justify-content: space-between;
        color: white;
        font-size: 20px;
        font-weight: bold;
      }
  }

  @media (max-width: 440px)
  {
    .mapContainer {
        max-width: 200px;
        display: flex;
        flex-wrap: wrap;
      }
      .mountain {
        width: 25px;
        height: 25px;
        background-color: grey;
      }
      .resource {
        width: 25px;
        height: 25px;
        background-color: green;
      }
      .special {
        width: 25px;
        height: 25px;
        background-color: yellow;
      }
      .buttonHolder {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 12px;
        justify-content: space-between;
        color: white;
        font-size: 15px;
        font-weight: bold;
      }
  }